/* eslint-disable no-restricted-syntax */
/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import '../../styles/pages/ProductDetails/ProductDetails.scss';

import { useEffect, useMemo, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Crumbs } from '../../components/Crumbs';
import { GoBackButton } from '../../components/GoBackButton';
import { ButtonsSelect } from '../../components/ButtonsSelect';
import { ProductSlider } from '../../components/ProductSlider';
import { getProductDetails } from '../../utils/product-mocks';
import { Button } from '../../components/Button';
import { Product, ProductDetails } from '../../types/product';
import { productApi } from '../../utils/api/productApi';
import { Loader } from '../../components/Loader';
import { useLocalStorage } from '../../utils/hooks/useLocalStorage';
import { Item } from '../../types/storageItem';
import { Storage } from '../../types/storages';

const BASE_URL = 'https://mate-academy.github.io/react_phone-catalog/_new/';

export const ProductDetailsPage = () => {
  const [products, setProducts] = useState<Product[]>([]);
  const [productDetails, setProductDetails]
    = useState<ProductDetails>(getProductDetails);
  const [isLoading, setIsLoading] = useState(false);
  const [selectedImageId, setSelectedImageId] = useState(0);
  const [fav, setFav]
    = useLocalStorage<Item<Product>[]>([], Storage.FAVOURITES);
  const [cart, setCart]
    = useLocalStorage<Item<Product>[]>([], Storage.CART);
  const { itemId } = useParams();

  const normalizedItemId = itemId || '';

  useEffect(() => {
    setIsLoading(true);

    productApi.getDetails(normalizedItemId.toString())
      .then(setProductDetails)
      .finally(() => setIsLoading(false));

    productApi.getAll
      .then(setProducts);
  }, [normalizedItemId]);

  const value = useMemo(() => {
    const item = products.filter(product => {
      return product.itemId === normalizedItemId.toString();
    })[0];

    return item;
  }, [normalizedItemId, products]);

  const isIncluded = (items: Item<Product>[]) => {
    for (const item of items) {
      if (value && item.value.id === value.id) {
        return true;
      }
    }

    return false;
  };

  const handleCartClick = () => {
    if (isIncluded(cart)) {
      setCart(prev => {
        return prev.filter(item => item.value.id !== value.id);
      });
    } else {
      setCart(prev => [...prev, { quantity: 1, value }]);
    }
  };

  const handleFavClick = () => {
    if (isIncluded(fav)) {
      setFav(prev => {
        return prev.filter(item => item.value.id !== value.id);
      });
    } else {
      setFav(prev => [...prev, { quantity: 1, value }]);
    }
  };

  const onImageClick = (id: number) => {
    setSelectedImageId(id);
  };

  const {
    name,
    color,
    images,
    colorsAvailable,
    capacity,
    capacityAvailable,
    priceDiscount,
    priceRegular,
    screen,
    resolution,
    processor,
    ram,
    camera,
    zoom,
    cell,
    description,
  } = productDetails;

  return (
    <main className="product-details">
      {isLoading ? (
        <Loader />
      ) : (
        <>
          <Crumbs />

          <div className="product-details__go-back">
            <GoBackButton />
          </div>

          <h1 className="product-details__title">
            {name}
          </h1>

          <section className="product-details__touchable-content">
            <div className="product-details__small-images">
              {images.map((image, index) => (
                <img
                  key={image}
                  src={BASE_URL + image}
                  alt="phone"
                  onClick={() => onImageClick(index)}
                  className="product-details__small-image"
                />
              ))}
            </div>

            <img
              src={BASE_URL + images[selectedImageId]}
              alt="phone"
              className="product-details__big-image"
            />

            <div className="product-details__right-menu">
              <div className="product-details__button-selector">
                <ButtonsSelect
                  title="Available colors"
                  contents={colorsAvailable}
                  type="color"
                  activeContent={color}
                />
              </div>

              <div className="product-details__button-selector">
                <ButtonsSelect
                  title="Select capacity"
                  contents={capacityAvailable}
                  type="text"
                  activeContent={capacity}
                />
              </div>

              <div className="product-details__prices">
                <h1 className="product-details__price">{`$${priceDiscount}`}</h1>

                <h2 className="product-details__full-price">{`$${priceRegular}`}</h2>
              </div>

              <div className="product-details__buy-fav-buttons">
                <Button
                  content="text"
                  onClick={handleCartClick}
                  isActive={!isIncluded(cart)}
                  isSelected={isIncluded(cart)}
                >
                  Add to cart
                </Button>

                <div className="product-details__fav-button">
                  <Button
                    content="fav"
                    onClick={handleFavClick}
                    isActive={isIncluded(fav)}
                  />
                </div>
              </div>

              <div className="product-details__small-about">
                <div className="product-details__small-about-left">
                  <p>Screen</p>
                  <p>Resolution</p>
                  <p>Processor</p>
                  <p>RAM</p>
                </div>
                <div className="product-details__small-about-right">
                  <p>{screen}</p>
                  <p>{resolution}</p>
                  <p>{processor}</p>
                  <p>{ram}</p>
                </div>
              </div>
            </div>
          </section>

          <section className="product-details__articles">
            <article className="product-details__about">
              <h1 className="product-details__about-title">
                About
              </h1>

              {description.map(article => (
                <article
                  key={article.title}
                  className="product-details__about-article"
                >
                  <h3 className="product-details__about-article-title">
                    {article.title}
                  </h3>

                  <p className="product-details__about-article-content">
                    {article.text}
                  </p>
                </article>
              ))}
            </article>

            <article className="product-details__tech-specs">
              <h1 className="product-details__tech-specs-title">Tech specs</h1>

              <div className="product-details__tech-specs-filling">
                <div className="product-details__tech-specs-left">
                  <p>Screen</p>
                  <p>Resolution</p>
                  <p>Processor</p>
                  <p>RAM</p>
                  <p>Built in memory</p>
                  <p>Camera</p>
                  <p>Zoom</p>
                  <p>Cell</p>
                </div>
                <div className="product-details__tech-specs-right">
                  <p>{screen}</p>
                  <p>{resolution}</p>
                  <p>{processor}</p>
                  <p>{ram}</p>
                  <p>{capacity}</p>
                  <p>{camera}</p>
                  <p>{zoom}</p>
                  <p>{cell.join(', ')}</p>
                </div>
              </div>
            </article>
          </section>

          <div className="product-details__slider">
            <ProductSlider
              title="You may also like"
              products={products.slice(5, 17)}
            />
          </div>
        </>
      )}
    </main>
  );
};
